rg (rechts) mit den drei riesigen Schornsteinen des großen\n        Eisenbahn-Kraftwerkes, während wir im Hintergrund die neun, je 110 m hohen Schornsteine von Golpa-Zschornewitz\n        sehen, die eine lebhafte Unterbrechung der den ganzen Horizont bedeckenden dunklen Waldlinie der Dübener Heide\n        bilden. Linker Hand sieht man bei günstigem Wetter den Petersberg bei Löbejün. Beim Kilometerstein 18,6 Gabelung.")]),_vm._v(" "),_c('h4',[_vm._v("Wegpunkte")]),_vm._v(" "),_c('h5',[_c('a',{attrs:{"href":"https://de.wikipedia.org/wiki/Ausbesserungswerk_Delitzsch"}},[_vm._v("Ausbesserungswerk Delitzsch")])]),_vm._v(" "),_c('p',[_vm._v("ehemaliges Ausbesserungswerk der Deutschen Reichsbahn im sächsischen Delitzsch, heute Werk der EuroMaint Rail")]),_vm._v(" "),_c('h5',[_vm._v("Freigut Benndorf")]),_vm._v(" "),_c('p',[_vm._v("Bereits 1350 war ein Herrensitz vorhanden, der ab 1400 als Rittersitz bezeichnet wurde. Zu dieser Zeit diente das\n        Anwesen als Ratsgut der Stadt Delitzsch. 1880 war die Familie Schnapperelle, 1899 die Familie Albert und 1906 die\n        Familie Böttcher Besitzer des Freigutes Benndorf. Ab 1913 befand es sich in Besitz der Familie Seyferth und wurde\n        1938 an Familie Schäfer verkauft, bevor diese 1945 enteignet wurde. Das Herrenhaus des Freigutes wurde saniert\n        und dient heute als Wohnhaus und Ortsbegegnungszentrum.")]),_vm._v(" "),_c('h5',[_c('a',{attrs:{"href":"https://www.wikidata.org/wiki/Q49561399"}},[_vm._v("Kirche Benndorf")])]),_vm._v(" "),_c('p',[_vm._v("bemerkenswerter romanischer Sakralbau, Saalkirche mit eingezogenem Chor und Apsis, kleiner Fachwerk-Kirchturm, baugeschichtlich,\n        kunstgeschichtlich und ortsgeschichtlich von Bedeutung. romanische Saalkirche komplett aus Feldsteinen, mit Dachreiter\n        "),_c('a',{attrs:{"title":"By Jwaller [CC BY-SA 3.0 (https://creativecommons.org/licenses/by-sa/3.0)], from Wikimedia Commons","href":"https://commons.wikimedia.org/wiki/File:BenndorfDZ-Kirche3.JPG"}},[